// Buttons - increased colour contrast for a11y on light backrounds
.btn-outline-secondary {
  $btn-color: $gray-600;
  color: $btn-color;
  border-color: $btn-color;
}

// Close icon - increased colour contrast for a11y
.close {
  opacity: .75;

  &:not(:disabled):not(.disabled) {
    @include hover-focus() {
      opacity: 1;
    }
  }
}

// Workaround https://github.com/twbs/bootstrap/issues/27879
// This ensures that the `.table-dark` context class produces a dark row
@include table-row-variant("dark", $dark, $gray-600);

.table-hover tbody .table-dark:hover {
  color: $light;
}
